.Card {
    width: 250px;
    padding: 16px;
    text-align: center;
    border: 1px solid #eee;
    box-shadow: 0 2px 3px #ccc;
    margin: 20px;
    box-sizing: border-box;
    cursor: pointer;
    min-height: 200px;
    text-decoration: none;
    color: black;
    border-radius: 10px;
    background-color: rgba(0, 0, 0, 0.1);
}

.Card:hover,
.Card:active {
    box-shadow: 15px 15px 15px #ccc;
}

.Author {
    margin: 16px 0;
    color: black;
    text-decoration: none;
}
